<template>
    <group  class="jq-mt">
      <flexbox class="jian-bg">
        <flexbox-item class='jia-bg'>
          <popup-picker :title="busTitle" :data="busList" :value.sync="bus" ></popup-picker>
        </flexbox-item>
        <flexbox-item class='jia-bg'>
          <popup-picker :title="dateTile" :data="timeList" :value.sync="time" ></popup-picker>
        </flexbox-item>
      </flexbox>
    </group>

 <group  class="jq-mt">
   <div class="chart" v-epie="pieData" ></div>
    </group>

</template>
  <script>
    import {Flexbox, FlexboxItem,Group,Cell,XButton,PopupPicker} from 'vux/src/components'
    import { getList } from '../../../untils/api'
    export default {
    components: {
    Flexbox,
    FlexboxItem,
    Cell,
    PopupPicker,
    Group,
    XButton

    },
    data () {
    return {
    busTitle:'事业部',
    dateTile:'日期',
    busList: [formatData.busList],
    timeList: formatData.dateList,
    bus: [this.$route.query.bus],
    time: formatData.defaultTime,
    pieData:{}
    }
    },
    ready(){
        document.title='成本构成';
    },
    methods: {
    async getData() {
      commit('UPDATE_LOADING', true)
    let id ='';
    if(this.bus=='FPI'){
      id="APP10108";
    }else if(this.bus=='SMD'){
     id="APP10109";
    }else if(this.bus=='ADD'){
     id="APP10110";
    }
    var date=formatData.dateConversion(this.time);
    let dataTit = await getList(id,{date_:date});
    if(dataTit.success){
 
    //表头：
    //表格:
    const TableList = dataTit.data;
    this.pieData = {};
    this.pieData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
      commit('UPDATE_LOADING', false)
    }else{
        commit('UPDATE_LOADING', false)
     console.log(dataTit.message);
    }

    }
    },
    watch: {
    bus (newbus, oldbus) {
    this.getData();
    },
     time (newtime, oldtime) {
      this.getData();
       formatData.setDateTime(newtime);
    }
  },
    created: function () {
    this.pieData = {};
    this.pieData.dataset = [];
    this.getData();
    }
    }
  </script>






